/*
 *  Hero Double CTA
 *    Styles for the hero full banner section.
 *
 *  Version:
 *    1.0.0 - 2021/10/25
 */

.c-hero-double-cta {
  // Inner
  &__inner {
    position: relative;
  }

  // Banner image
  &__image {
    &.has-mobile.is-desktop {
      display: none;
    }
  }

  &__image-container {
    padding-bottom: 118%;
  }

  &__content {
    @include t-absolute-center-x-y();
    width: 100%;
    padding: 0 $s-gutter--edge-mobile;
    text-align: center;
    color: $s-color--font-invert;
    margin-top: 2rem;
  }

  &__cta-wrapper {
    margin-top: 2.375rem;
    display: flex;
    justify-content: center;
    gap: 1.375rem;
  }

  @include t-media-query($s-small-up) {
    &__image {
      &.is-mobile {
        display: none;
      }
      &.has-mobile.is-desktop {
        display: block;
      }
    }

    &__image-container {
      padding-bottom: 50%;
    }   
    
    &__content{
      margin-top: 0;
    }
  }

   @include t-media-query($s-medium-up) {

    &__image-container {
      padding-bottom: 36.7%;
    }

    &__pretitle{
      margin-bottom: 1.5rem;
    }

    &__title{
      font-size: 3rem;
      margin-bottom: 1.5rem;
    }

    &__cta-wrapper{
      width: 26.5%;
      max-width: 33.125rem;
      margin: 3.5625rem auto 0;
    }

    &__cta-btn{
      width: 100%;
    }

    &__content{
      top: 54%;
    }
 
  }
}
